import { Component } from 'react';
import Papa from 'papaparse';
import { sum, uniq } from 'lodash';

const getData = async (path) => {
  const response = await fetch(path)
  const reader = response.body.getReader()
  const result = await reader.read() // raw array
  const decoder = new TextDecoder('utf-8')
  const csv = decoder.decode(result.value) // the csv text
  const results = Papa.parse(csv, { header: true }) // object with { data, errors, meta }
  const rows = results.data // array of objects
  return rows.filter(r => r.Program !== '');
}

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      view: 'programs',
      rows: [],
      programs: [],
      selectedProgram: null,
      selectedServices: [],
      outcomes: [],
      selectedOutcomes: []
    }
  }

  selectView = (view) => () => this.setState({ view });

  selectProgram = (selectedProgram) => () => this.setState({ selectedProgram });
  selectService = (selectedService) => () => {
    const { rows, selectedProgram, selectedServices } = this.state;
    const thisRow = {
      ...rows.find(r => r.Program === selectedProgram && r.Service === selectedService),
      Participants: 1,
      AchievedGoal: 0
    };
    const newSelectedServices = [...selectedServices, thisRow];
    this.setState({ selectedServices: newSelectedServices })
  }

  removeService = (selectedService) => () => {
    const { selectedServices } = this.state;
    const thisSS = selectedServices.find(ss => ss.Program === selectedService.Program && ss.Service === selectedService.Service);
    const index = selectedServices.indexOf(thisSS);
    const newSelectedServices = [
      ...selectedServices.slice(0, index), ...selectedServices.slice(index + 1)
    ];
    this.setState({ selectedServices: newSelectedServices });
  }

  selectOutcome = (selectedOutcome) => () => {
    const { selectedOutcomes } = this.state;
    const newSelectedOutcomes = [...selectedOutcomes, selectedOutcome];
    this.setState({ selectedOutcomes: newSelectedOutcomes });
  }

  removeOutcome = (selectedOutcome) => () => {
    const { selectedOutcomes } = this.state;
    const thisO = selectedOutcomes.find(so => so.Outcome === selectedOutcome.Outcome);
    const index = selectedOutcomes.indexOf(thisO);
    const newSelectedOutcomes = [
      ...selectedOutcomes.slice(0, index), ...selectedOutcomes.slice(index + 1)
    ];
    this.setState({ selectedOutcomes: newSelectedOutcomes });
  }

  getTotalCost = (ss) => Number(ss.Cost) * Number(ss.Participants);
  getTotalCostPerAchievedGoal = (ss) => Number(ss.Participants) / Number(ss.AchievedGoal) * Number(ss.Cost);

  handleServiceChange = (ss, field, event) => {
    const { target } = event;
    const { value } = target;

    const { selectedServices } = this.state;
    const thisSS = selectedServices.find(s => s.Program === ss.Program && s.Service === ss.Service);
    const newSS = {
      ...thisSS,
      [field]: value
    };

    const index = selectedServices.indexOf(thisSS);

    const newSelectedServices = [
      ...selectedServices.slice(0, index),
      newSS,
      ...selectedServices.slice(index + 1)];
    this.setState({ selectedServices: newSelectedServices });
  }

  componentDidMount() {
    this.populateControl();
  }

  populateControl = async () => {
    const rows = await getData('/data/services.csv');
    const outcomes = await getData('/data/outcomes.csv');
    const programs = uniq(rows.map(r => r.Program));
    this.setState({ rows, programs, outcomes });
  }

  render() {
    const { view, rows, programs, selectedProgram, selectedServices, outcomes, selectedOutcomes } = this.state;

    const services = rows.filter(r => (selectedProgram === null || r.Program === selectedProgram)
      && (selectedServices.findIndex(ss => ss.Program === r.Program && ss.Service === r.Service) === -1))
      .map(r => r.Service);

    return (
      <div className="App">
        <h1>SROI Calculator</h1>
        <div className='view'>
          <ul>
            <li><button type='button' onClick={this.selectView('programs')}>Programs</button></li>
            <li><button type='button' onClick={this.selectView('outcomes')}>Outcomes</button></li>
          </ul>
        </div>
        {view === 'programs' ? (
          <>
            <div className='programs'>
              <h3>Programs</h3>
              <p>Select a program to view associated services.</p>
              <ul>
                {programs.map(p =>
                  <li key={p} className={p === selectedProgram ? 'selected' : null}>
                    <button type='button' onClick={this.selectProgram(p)}>{p}</button>
                  </li>
                )}
              </ul>
            </div>
            {selectedProgram !== null && (
              <div className='services'>
                <h3>Available Services</h3>
                <p>Select services to add to the calculator.</p>
                <ul>
                  {services.map(s => (
                    <li key={s}><button type='button' onClick={this.selectService(s)}>{s}</button></li>
                  ))}
                </ul>
              </div>
            )}
          </>) : (
          <div className='outcomes'>
            <h3>Outcomes</h3>
            <p>Select an outcome to add it as a selected outcome.</p>
            <ul>
              {outcomes.map(o => (
                <li key={o.Outcome}>
                  <button type='button' onClick={this.selectOutcome(o)}>{o.Outcome}</button>
                </li>
              ))}
            </ul>
          </div>
        )}
        {selectedServices.length > 0 && (
          <div className='selectedServices'>
            <h3>Selected Services</h3>
            <p>Fill in the fields to see estimated return on investment for each service. Estimated service costs are provided but can be customized.</p>
            <table>
              <thead>
                <tr>
                  <th>Service</th>
                  <th>Cost per Service</th>
                  <th># Participants</th>
                  <th># Achieved Outcome</th>
                  <th>Total Cost</th>
                  <th>Cost per Success</th>
                  <th>ROI</th>
                </tr>
              </thead>
              <tbody>
                {selectedServices.map(ss => (
                  <tr key={`${ss.Program}-${ss.Service}`}>
                    <td><button type='button' onClick={this.removeService(ss)}>{ss.Service}</button></td>
                    <td><input type='number' value={ss.Cost} onChange={(event) => this.handleServiceChange(ss, 'Cost', event)} /></td>
                    <td><input type='number' value={ss.Participants} onChange={(event) => this.handleServiceChange(ss, 'Participants', event)}></input></td>
                    <td><input type='number' max={ss.Participants} value={ss.AchievedGoal} onChange={(event) => this.handleServiceChange(ss, 'AchievedGoal', event)}></input></td>
                    <td>{`$${this.getTotalCost(ss).toFixed(2)}`}</td>
                    <td>{`$${this.getTotalCostPerAchievedGoal(ss).toFixed(2)}`}</td>
                    <td>{`${(Number(ss.Cost) / (this.getTotalCostPerAchievedGoal(ss)) * 100).toFixed(2)}%`}</td>
                  </tr>
                ))}
                <tr>
                  <td>Totals</td>
                  <td>{`$${sum(selectedServices.map(ss => Number(ss.Cost)))}`}</td>
                  <td>{sum(selectedServices.map(ss => Number(ss.Participants)))}</td>
                  <td>{sum(selectedServices.map(ss => Number(ss.AchievedGoal)))}</td>
                  <td>{`$${sum(selectedServices.map(ss => this.getTotalCost(ss))).toFixed(2)}`}</td>
                  <td>{`$${sum(selectedServices.map(ss => this.getTotalCostPerAchievedGoal(ss))).toFixed(2)}`}</td>
                </tr>
              </tbody>
            </table>
          </div>
        )}
        {selectedOutcomes.length > 0 && (
          <div className='selectedOutcomes'>
            <h3>Selected Outcomes</h3>
            <table>
              <thead>
                <tr>
                  <th>Outcome</th>
                  <th>Value</th>
                </tr>
              </thead>
              <tbody>
                {selectedOutcomes.map(so => (
                  <tr key={so.Outcome}>
                    <td><button type='button' onClick={this.removeOutcome(so)}>{so.Outcome}</button></td>
                    <td>${Number(so.Value).toFixed(2)}</td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    );
  }
}

export default App;
